import {useLocation} from "react-router-dom"
import {createContext, useEffect, useState, useRef} from "react";
import emailjs from "emailjs-com"
import axios from "axios";
import {animateScroll} from "react-scroll";



export const CustomContext = createContext();

export const Context = (props) => {

    const form = useRef();
    const [status,setStatus] = useState('');

    const sendEmail = (e) => {
        e.preventDefault();

        const formData = new FormData(form.current);

        console.log(' :', {
            from_name: formData.get('from_name'),
            namber_user: formData.get('namber_user'),
            message: formData.get('message'),
        });

        emailjs
            .sendForm(
                'service_eplzjji',      //  Service ID
                'template_0fetif2',     //  Template ID
                form.current,           // 
                'iCTr7YvPOeeP0Hf7l'     //  Public Key (User ID)
            )
            .then(
                (result) => {
                    console.log(' :', result.text);
                    setStatus(' !');
                    form.current.reset(); //  
                },
                (error) => {
                    console.error(' :', error.text);
                    setStatus('   .');
                }
            );
    };


    const location = useLocation();
    useEffect(() => {
        const titleName = document.querySelector("title");
        const nameObdject = () => {
            if (location.pathname === "/buying-boards") {
                titleName.innerHTML = renderHeader()
            }
            else {
                titleName.innerHTML = ""
            }
        };
        nameObdject()
    });

    const renderHeader = () => {
        switch (filter) {
            case "":
                return "       !";
            case "":
                return "   ";
            case "":
                return "       !";
            case "":
                return "   -   ";
            case "":
                return "    |     !";
            case "":
                return "      !";
            case "":
                return "    ";
            default:
                return "    ";
        }
    };

    const [product, setProduct] = useState([])


    useEffect(() => {
        axios('http://localhost:3031/product')
            .then(({data}) => setProduct(data))
    }, []);


    const [filter, setFilter] = useState('')

    const proba = (el) => {
        setFilter(el)
    };
    const deleteProduct = (id)=>{
        axios.delete(`http://localhost:3031/product/${id}`);
        window.location.reload()
    };

    const toTop = () => {
        animateScroll.scrollToTop({
            delay: 0,
            duration: 0,
            smooth: true
        })
    };

    const [menu,setMenu] = useState(false);
    const [filterMenu,setFilterMenu] = useState(false);



    //   /  
    const mobelMenu = () => {
        setMenu(prevState => {
            const newMenuState = !prevState;
            if (!newMenuState) {
                //    ,  
                setFilterMenu(false);
            }
            return newMenuState;
        });
    };

    //   / 
    const menuFilter = () => {
        setFilterMenu(prevState => !prevState);
    };


    const [filterAll,setFilterAll] = useState(false);
    const menuFilterAll = () => {
        setFilterAll(prevState => !prevState);
    };

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [modalImage, setModalImage] = useState("");

    const openModal = (img) => {
        setModalImage(img);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
        setModalImage("");
    };

    const value = {
        isModalOpen,
        modalImage,
        openModal,
        closeModal,
        toTop,
        product,
        filter,
        proba,
        renderHeader,
        deleteProduct,
        mobelMenu,
        menu,
        sendEmail,
        form,
        status,
        filterMenu,
        menuFilter,
        menuFilterAll,
        filterAll
    };
    return <CustomContext.Provider value={value}>
        {props.children}
    </CustomContext.Provider>
}